





















import fIcon from '@/components/m-icon/f-icon.vue'
import {mapGetters} from 'vuex'

export default {
	components: {fIcon},
	name: 'activity-picker',
	props: {
		title:{ //标题
	        type: String,
	        default: ''
	    },
		activities: { //活动列表
		    type: Array,
		    default: []
		},
	    show: { //是否显示模态框
	        type: [Boolean, String],
	        default: false
	    }
	},
	data() {
		return {
			isOpen: false,
			showAnimation:false,
			inWatchTimeout:false,
			inMethodsTimeout:false
		};
	},
	computed: {
		...mapGetters(['agentMember'])
	},
	watch: {
	    show(val) {
	        if(this.inWatchTimeout){
	            return
	        }
	        if (val) {
	        	this.isOpen = val
	            setTimeout(() => {
	            	this.showAnimation = val
	            }, 50)
	        } else {
	            this.showAnimation = val
	            this.inWatchTimeout = true
	        	setTimeout(() => {
	        		this.isOpen = val
	                this.inWatchTimeout = false
	        	}, 200)
	        }
	    }
	},
	created() {
		this.isOpen = this.show
		setTimeout(() => {
			this.showAnimation = this.show
		}, 50)
	},
	methods:{
		closeModal(activity) {
		    this.showAnimation = false
		    this.inMethodsTimeout = true
		    setTimeout(() => {
		    	this.isOpen = false
		        this.inMethodsTimeout = false
		        this.$emit('close', activity);
		    }, 200)
		},
		doPick:function(activity){
			this.closeModal(activity);
		}
	},
	onShow() {
		
	},
	onLoad() {
		
	}
}
